  *{
    font-family: 'Ysabeau Infant', sans-serif;
  }
  .app{
    min-height: 100vh;
  }
  .container{
    background-color: #fff;
    padding: 1.2rem;
    margin: 3rem auto;
    max-width: 35rem;
    border: solid 1px rgba(0,0, 0, 0.2);
    border-radius: 0.25rem;
  }
  .light{
    background-color: #fff;
    color: #15202b;
    transition: all 500ms;
  }
  .Dark{
    background-color: #15202b;
    color: #fff;
    transition: all 500ms;
  }